#nav {
	position: sticky;
	top: 0px;
	max-width: 800px;
	background-color: white;
	margin: 0px auto 20px;
	padding: 20px;
	/* min-height: 70vh; */
	border-radius: 10px;
}

#filter {
	position: sticky;
	top: 0px;
	z-index: 1;
	background-color: white;
}

#filter-icon {
	background-color: white !important;
	border: none;
}

.playbtn {
	width: 55px !important;
	height: 55px !important;
	border-radius: 50% !important;
	line-height: 0.8 !important;
}

.faicons {
	box-shadow: none !important;
	border-radius: 20% !important;
	/* border: none !important; */
	/* background-color: white !important; */
}

#settingsIcon {
	position: fixed;
	/* right: 35px; */
	/* top: 35px; */
	right: 3%;
	top: 6%;
	border: none;
	border-radius: 7px;
	box-shadow: none;
	line-height: 90%;
}

#vernagir {
	margin: auto;
	padding-top: 30px;
	padding-bottom: 10px;
	text-align: center;
	color: white;
	font-family: "Libre Baskerville";
}
